.about-wrapper {
    background-color: #e9e9e9;
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    flex-wrap: wrap;
}

.about {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 1110px;
    padding: 120px 0 100px 0;
    &-text {
        padding: 55px 65px;
        border-radius: 10px;
        height: fit-content;
        box-shadow: 2px 3.464px 18px rgba(1, 1, 1, 0.14);
        font: normal 15px "Roboto";
        color: #3e3e3e;
        width: 50%;
        &-scaled {
            font: bold 30px "Arvo";
        }
    }
    &-slider {
        font: normal 15px "Poppins";
        margin: 3px 0 0 0;
        color: #3e3e3e;
        width: 40%;
        &-header {
            font: bold 20px "Arvo";
        }
    }
}